const GalleryButton = () => (
  <Button 
    to="/gallery" 
    kind="overlay" 
  >
    Gallery <Icon name="chevronRight" />
  </Button>
);

const ReadMoreButton = ({ helperRef, setScrolling }) => {
  const action = () => {
    // Report that we're programmatically scrolling to pervent listener from firing
    setScrolling(true);
    scroller.scrollTo('jewel', {
      duration: 1200,
      delay: 250,
      smooth: 'easeOutQuint',
      containerId: 'view',
      offset: -52, // Scrolls to element + 50 pixels down the page
    });

    setTimeout(()=>{
      setScrolling(false);
    }, 1500);
  }
  return (
    <Button 
      kind="overlay" 
      onPress={action} 
    >
      Find Out <Icon name="chevronDown" />
    </Button>
  );
}

const Video = () => (
  <div className={styles.videoWrapper}>
    <video muted autoPlay loop playsInline>
      <source src="https://wristgameworld.nyc3.cdn.digitaloceanspaces.com/videos/wrist-hd-aux-vid.m4v" type="video/mp4" />
    </video>
  </div>
)